import { WorldStateConsumer } from '@anonymous.club/types';
import { useEffect } from 'react';
import { Route, Link, useRouteMatch } from 'react-router-dom';

// import  './BioText.module.scss';
import styles from './BioText.module.scss';

/* eslint-disable-next-line */
export interface BioTextProps {}
const BIO_TEXT = `Anonymous Club is a creative studio led by Shayne Oliver, working in the idioms of fine art,
installation, performance, music and product. Anonymous Club creates radical art and artifacts
designed to upend tradition, infiltrate the mainstream and remake pop culture in its image.`;

export function BioText(props: BioTextProps & Required<WorldStateConsumer>) {
  const match = useRouteMatch('/bio');
  const setOpen = props.useWorldStore(state => state.setBottomOpen);
  useEffect(() => {
    console.log(styles);
    setOpen(!!match);
  }, [match, setOpen]);
  return (
    <div className={styles['bio-text']}>
      <div className={styles['bio-text__child']+ ' ' + styles[(match ? 'open' : 'closed')]}>

        <Route
          path="/bio"
          // render={() => <div>This is the BioText root route.</div>}
          >
          <figcaption>
            {BIO_TEXT}
          </figcaption>
        </Route>
      </div>
    </div>
  );
}
export default BioText;
